'use client';

import { useDebateTimer } from '../lib/useDebateTimer';
import Controls from '../components/Controls';
import styles from './page.module.css';
import Link from 'next/link';

export default function AdminPage() {
  const {
    participants,
    timerState,
    isLoading,
    useLocalMode,
    startTimer,
    pauseTimer,
    stopTimer,
    switchSpeaker,
    resetParticipantTime,
    resetAllTimers,
    addParticipant,
    removeParticipant,
    updateParticipantTime,
  } = useDebateTimer();

  if (isLoading) {
    return <div className={styles.loading}>A carregar...</div>;
  }

  return (
    <main className={styles.main}>
      <header className={styles.header}>
        <h1 className={styles.title}>Painel de Gest√£o</h1>
        <p className={styles.subtitle}>Controle do cron√≥metro de debate</p>
        <Link href="/" className={styles.backLink}>
          ‚Üê Voltar √† visualiza√ß√£o p√∫blica
        </Link>
      </header>

      <div className={styles.content}>
        {useLocalMode && (
          <div className={styles.localModeNotice}>
            <p>‚ö†Ô∏è Modo Local Ativo</p>
            <small>
              Configure as vari√°veis NEXT_PUBLIC_SUPABASE_URL e NEXT_PUBLIC_SUPABASE_ANON_KEY 
              para sincroniza√ß√£o em tempo real
            </small>
          </div>
        )}

        <Controls
          participants={participants}
          timerState={timerState}
          onStartTimer={startTimer}
          onPauseTimer={pauseTimer}
          onStopTimer={stopTimer}
          onSwitchSpeaker={switchSpeaker}
          onResetParticipant={resetParticipantTime}
          onResetAll={resetAllTimers}
          onAddParticipant={addParticipant}
          onRemoveParticipant={removeParticipant}
          onUpdateParticipantTime={updateParticipantTime}
        />
      </div>
    </main>
  );
}
